import org.jetbrains.compose.web.dom.H2

@Composable
fun IntroSection() {
    Box(
        modifier = SectionModifier
            .toModifier()
            .width(100.percent),
        contentAlignment = Alignment.TopCenter
    ) {
        if (!rememberPageContext().isExporting) {
            Column(
                modifier = listOf(
                    IntroSectionStyle
                ).toModifier(),
                verticalArrangement = Arrangement.SpaceAround
            ) {
                H1(
                    attrs = H1Style
                        .toAttrs()
                ) {
                    SpanText(
                        "Hey there!"
                    )
                }
                H2(
                    attrs = H2Style
                        .toAttrs()
                ) {
                    SpanText(
                        "I'm "
                    )
                    SpanText(
                        "Carlos Ugaz",
                        modifier = Modifier.bold()
                    )
                    SpanText(
                        ", a mobile software engineer focused on building awesome mobile applications"
                    )
                }
                Row(
                    modifier = H6Style
                        .toModifier(),
                ) {
                    SpanText("Get in touch ")
                    Link(
                        path = "mailto:carlosgub.dev@gmail.com",
                        text = "carlosgub.dev@gmail.com",
                        modifier = LinkStyle.toModifier()
                            .wordBreak(WordBreak.BreakAll)
                    )
                }
            }
        }
        // TODO ADD TOOLBAR
    }
}
